<template>
    <div class="header-container">
        <div class="header-content" v-if="isPc">
            <div class="header-left" :class="{'show-pointer': !router.includes('order/place')}" @click="enterHomePage">
                <i></i>
                <!--<span>{{$t('title')}}</span>-->
            </div>
            <div class="header-right">
                <span class="item-help" :class="{'checked-router': router.endsWith('help')}"
                      @click="enterOtherPage('help','support')"
                      v-if="!(router.includes('order/') || router.includes('user/invoice'))">{{$t('header.help-center')}}</span>
                <span class="item-product" :class="{'checked-router': router.endsWith('order/place')}"
                      @click="enterRenewOrOrder()"
                      v-if="!(router.includes('order/') || router.includes('user/invoice'))">{{$t(JSON.stringify(selectedService) !== '{}' && selectedService.status !== 'Pending' ? '前往续费' : 'header.subscribe-now')}}</span>
                <div class="item-service" @click="enterOtherPage(`user/service`,'service')"
                     v-if="!(router.includes('order/') || router.includes('user/invoice')) && serviceChildList.length === 0">
                    <span :class="{'checked-router': router.endsWith('service')}">{{$t('header.my-service')}}</span>
                    <i :class="{'down-i': isLogin}"></i>
                </div>
                <a-popover placement="bottom" v-if="isLogin && serviceChildList.length > 0">
                    <template slot="content" style="margin: 0px;">
                        <p class="lang-item" :class="{'checked-lang-item': router.endsWith(item.value)}"
                           v-for="(item,index) in serviceChildList" :key="index " @click.stop="goToChildPage(item)">
                            {{item.key}}</p>
                    </template>
                    <div class="item-service" @click="enterOtherPage(`user/service`,'service')"
                         v-if="!(router.includes('order/') || router.includes('user/invoice'))">
                        <span :class="{'checked-router': router.endsWith('service')}">{{$t('header.my-service')}}</span>
                        <i class="down-i"></i>
                    </div>
                </a-popover>
                <a-divider style="margin:0px 32px;background: #E5E9ED;height: 16px;" type="vertical"
                           v-if="!(router.includes('order/') || router.includes('ser/invoice'))"/>
                <a-popover placement="bottom">
                    <template #content>
                        <p class="lang-item" :class="{'checked-lang-item': item.value === lang}"
                           @click="switchLang(item)" v-for="(item,index) in langList" :key="index">{{item.key}}</p>
                    </template>
                    <div class="item-service">
                        <span>{{lang === 'zh-CN-ct' || lang === 'zh-CN' ? '简体中文' : lang === 'en-US-ct' || lang === 'en-US' ? 'English' : '繁體中文'}}</span>
                        <i class="down-i"></i>
                    </div>
                </a-popover>
            </div>
        </div>
        <div class="header-content-mobile" v-if="!isPc && !router.endsWith('order/place') && !router.includes('/help')">
            <div class="header-menu">
                <a :style="{'opacity': router.includes('order/pay')? 0: 1}"
                   :class="{'a-back': (router.includes('order/') || router.includes('user/invoice')),'a-logo': !(router.includes(' order/') || router.includes('user/invoice'))}"
                   @click="enterHomePage()"></a>
                <button class="hamburger hamburger--squeeze" type="button" @click="openMenu"
                        :class="{'is-active': isOpen}"
                        v-if="!(router.includes('order/ ') || router.includes('user/invoice'))">
                    <span class="hamburger-box">
                      <span class="hamburger-inner"></span>
                    </span>
                </button>
            </div>
            <div class="menu-content" id="menu-content" :style="{display: isOpen ? 'flex': 'none'}">
                <a class="header-link" @click="enterOtherPage('help','support')">{{$t('header.help-center')}}</a>
                <a-button class="subscribe-btn" type="primary" ghost @click="enterRenewOrOrder()">
                    {{$t(JSON.stringify(selectedService) !== '{}' && selectedService.status !== 'Pending' ? '前往续费' : 'header.subscribe-now')}}
                </a-button>
                <a class="header-link" :class="'a-top-line'" @click="enterOtherPage(`user/service`,'service')">{{$t('header.my-service')}}</a>
                <div v-if="isLogin">
                    <a class="header-link" v-for="(item,index) in serviceChildList" :key="index"
                       @click.stop="goToChildPage(item)">{{item.key}}</a>
                </div>
                <a-radio-group class="lang-switch" v-model="langMb">
                    <a-radio :value="item.value === lang ? 1 : 0" v-on:click="switchLang(item)"
                             v-for="(item,index) in langList" :key="index">{{item.key}}
                    </a-radio>
                </a-radio-group>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        AU_UUID,
        AU_HMAC,
        AU_SELECTED_SERVICE,
        AU_EMAIL,
        AU_EMAIL_STATUS,
        AD_PLATFORM, AD_CLICKID
    } from "../requestServer";
    import {TweenMax} from 'gsap';
    import {notification} from "ant-design-vue";

    export default {
        name: "Header",
        data() {
            return {
                serviceChildList: [],
                langList: [{key: '繁体中文', value: 'zh-TW-ct'}, {key: '简体中文', value: 'zh-CN-ct'}, {
                    key: 'English',
                    value: 'en-US-ct'
                }],
                lang: this.$i18n.locale,
                router: this.$route.path,
                isLogin: localStorage.getItem('uuid') && localStorage.getItem('hmac'),
                selectedService: localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)) : {},
                isPc: this.$isPc(),
                isOpen: false,
                langMb: 1,
            }
        },
        watch: {
            $route(to) {
                if (this.router !== to.path) {
                    this.router = to.path;
                }
                //update client info
                this.getInfo();
                //console.log('to',to);
            }
        },
        created() {
            this.updateMenu();
        },
        mounted() {
            this.$eventBus.$on("update_header", (update) => {
                if (update) this.selectedService = localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)) : {};
            });
        },
        methods: {
            updateMenu() {
                this.isLogin = localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC);
                if (this.isLogin) {
                    if (localStorage.getItem(AU_SELECTED_SERVICE)) {
                        this.serviceChildList = [{
                            key: this.$t("header.account"),
                            path: 'user/account',
                            name: 'manageAccount'
                        }, {
                            key: this.$t("header.billing"),
                            path: 'invoice',
                            name: 'manageBill'
                        }, {key: this.$t("header.log-out"), path: 'logout'}];
                        this.selectedService = JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE));
                    } else {
                        this.getInfo();
                    }
                }
            },
            getInfo() {
                const that = this;
                this.isLogin = localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC);
                if (this.isLogin) {
                    this.$request.auGetAllServices('', '', function (data) {
                        if (data && (data.status === 200 || data.status === 201) && data.data) {
                            localStorage.setItem(AU_EMAIL, data.data.email);
                            localStorage.setItem(AU_EMAIL_STATUS, data.data.status);
                            if (data.data.jjService && data.data.jjService.length > 0) {
                                if (!localStorage.getItem(AU_SELECTED_SERVICE)) {
                                    const activeServices = data.data.jjService.filter(service => service.status === 'Active')
                                    localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(activeServices.length > 0 ? activeServices.pop() : data.data.jjService[0]));
                                }
                            } else if (data.data.unpaidService && data.data.unpaidService.length > 0) {
                                if (!localStorage.getItem(AU_SELECTED_SERVICE)) {
                                    localStorage.setItem(AU_SELECTED_SERVICE, JSON.stringify(data.data.unpaidService[0]));
                                }
                            }
                            if (localStorage.getItem(AU_SELECTED_SERVICE)) {
                                that.serviceChildList = [{
                                    key: that.$t("header.account"),
                                    path: 'user/account',
                                    name: 'manageAccount'
                                }, {
                                    key: that.$t("header.billing"),
                                    path: 'invoice',
                                    name: 'manageBill'
                                }, {key: that.$t("header.log-out"), path: 'logout'}];
                                that.selectedService = JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE));
                            } else {
                                that.serviceChildList = [{
                                    key: that.$t("header.account"),
                                    path: 'user/account',
                                    name: 'manageAccount'
                                },{key: that.$t("header.log-out"), path: 'logout'}];
                            }
                        } else {
                            that.serviceChildList = [{
                                key: that.$t("header.account"),
                                path: 'user/account',
                                name: 'manageAccount'
                            },{key: that.$t("header.log-out"), path: 'logout'}];
                        }
                    });
                }
            },
            switchLang(lang) {
                if (this.lang !== lang.value) {
                    if (this.$route.query['ct'] && Number(this.$route.query['ct']) === 0) {
                        this.lang = this.$route.name !== 'home'? 'zh-TW-ct' : lang.value.toString().replace('-ct', '');
                    } else {
                        this.lang = this.$route.name !== 'home'? 'zh-TW' : lang.value;
                    }
                    this.$emit('switchLang', true);
                    this.$setupLang(this.lang);

                }
                if (!this.isPc) {
                    this.isOpen = false
                }
                this.updateMenu()
            },
            enterOtherPage(path, name) {
                if (!this.$route.path.endsWith(path)) {
                    if (path === 'user/service') {
                        if (!this.$route.path.endsWith('user/login')) {
                            if (localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC)) {
                                if (!this.$route.path.includes(path) || (this.$route.path.includes(path) && this.$route.path.includes('invoice'))) {
                                    let id = localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)).id : 0;
                                    this.$router.push({
                                        path: `/user/service`,
                                        name: name,
                                        params: {
                                            serviceId: id,
                                        },
                                        query: {
                                            ...this.$route.query,
                                            uuid: localStorage.getItem(AU_UUID),
                                            hmac: localStorage.getItem(AU_HMAC)
                                        }
                                    });
                                }
                            } else {
                                this.$router.push({
                                    path: 'user/login',
                                    name: 'login',
                                    query: this.$route.query
                                });
                            }
                        }
                    } else if (path === 'help' || path === 'order/place') {
                        window.open(`${window.location.origin}/${path}`, this.$inApp ? '_self' : '_blank');
                        if (path === 'order/place') {
                            gtag('event', 'try_now_subscribe')
                        }
                    } else {
                        this.$router.push({
                            path: path,
                            name: name,
                            query: {
                                ...this.$route.query,
                                uuid: localStorage.getItem(AU_UUID),
                                hmac: localStorage.getItem(AU_HMAC)
                            }
                        });
                    }
                }
                if (!this.isPc) {
                    this.isOpen = false;
                }
            },
            enterRenewOrOrder() {
                this.selectedService = localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)) : {};
                if (JSON.stringify(this.selectedService) !== '{}' && this.selectedService.status !== 'Pending') {
                    /*if (this.selectedService.status === 'Active') {
                        this.$router.push({
                            path: 'invoice',
                            name: 'manageBill',
                            params: {
                                serviceId: this.selectedService.id,
                            },
                            query: this.$route.query
                        });
                    } else {*/
                        const that = this;
                        this.$request.auGetLastInvoice(that.selectedService.relid,function(data){
                            that.iconLoading = false;
                            if (data && (data.status === 200 || data.status === 201) && data.data){
                                let routeUrl = that.$router.resolve({
                                    path: 'order/pay',
                                    name: 'payment',
                                    params: {
                                        isRenew: true,
                                    },
                                    query: {
                                        invoiceId: data.data.id,
                                    }
                                });
                                window.open(routeUrl.href, that.$inApp? '_self': '_blank');
                            } else {
                                notification['warning']({
                                    message: '消息提示',
                                    description:
                                        '生成續費帳單失敗，請聯繫客服。',
                                });
                            }
                        });
                    //}
                } else {
                    window.open(`${window.location.origin}/order/place`, this.$inApp ? '_self' : '_blank');
                    gtag('event', 'try_now_subscribe')
                }
                if (!this.isPc) {
                    this.isOpen = false;
                }
            },
            goToChildPage(item) {
                if (item.path === 'logout') {
                    let platform = localStorage.getItem(AD_PLATFORM)
                    let clickid = localStorage.getItem(AD_CLICKID)
                    localStorage.clear();
                    if (platform) localStorage.setItem(AD_PLATFORM, platform)
                    if (clickid) localStorage.setItem(AD_CLICKID, clickid)
                    this.isLogin = localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC);
                    this.serviceChildList = [];
                    this.$router.push({
                        path: 'user/login',
                        name: 'login'
                    });
                    this.selectedService = {};
                } else {
                    if (!this.$route.path.endsWith(item.path)) {
                        let id = localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)).id : 0;
                        this.$router.push({
                            path: item.path === 'invoice' ? `user/service/:serviceId/invoice` : `user/service`,
                            name: item.name,
                            params: {
                                serviceId: id,
                            },
                            query: this.$route.query
                        });
                    }
                }
                /*
               else if (item.path === 'user/account'){
                    if (!this.$route.path.endsWith(item.path)) {
                        this.$router.push({
                            path: item.path,
                            name: item.name,
                        });
                    }
                } else if (item.path === 'invoice') {
                    if (!this.$route.path.endsWith(item.path)) {
                        let id = localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)).id : 0 ;
                        this.$router.push({
                            path: item.path === 'invoice' ? `user/service/:serviceId/invoice` : `user/service`,
                            name: item.name,
                            params: {
                                serviceId: id,
                            }
                        });
                    }
                }*/
                if (!this.isPc) {
                    this.isOpen = false;
                }
            },
            enterHomePage() {
                if (this.isPc || (!this.isPc && !(this.router.includes('order/') || this.router.includes('user/invoice')))) {
                    if (this.$route.name !== 'product') {
                        if (!(this.$route.name === 'home')) {
                            this.$router.push({
                                path: '',
                                name: 'home',
                                query: this.$route.query
                            });
                        }
                    }
                } else if (!this.isPc && (this.router.includes('order/') || this.router.includes('user/invoice'))) {
                    this.$router.back(-1);
                }
                if (!this.isPc) {
                    this.isOpen = false;
                }
            },
            openMenu() {
                if (this.isOpen) {
                    const that = this;
                    TweenMax.fromTo(
                        document.getElementById('menu-content'),
                        0.3,
                        {right: '0'},
                        {
                            css: {right: '-100%'}, ease: Power0.easeNone, onComplete: function () {
                                that.isOpen = false
                            }
                        }
                    )
                } else {
                    this.isOpen = true;
                    TweenMax.fromTo(
                        document.getElementById('menu-content'),
                        0.3,
                        {right: '-100%'},
                        {right: '0', ease: Power0.easeNone}
                    )
                }

            }
        }
    }
</script>
<style scoped src="../assets/css/hamburgers.css"></style>
