<template>
  <div style="width: 100%;
        background: #F6F8FA;
        min-height: 100%;
        height: fit-content;
        overflow: auto;
        text-align: justify;" class="au-panel-home">
    <Header
        :class="{'pc-header': isPc && router.endsWith('home')}"
        :style="{'filter': headerFilter ? 'blur(5px)': 'unset'}"
        v-if="!router.endsWith('app')"
    ></Header>
    <router-view @dialogShow="filterOpen" :class="{'header-below': isPc && router.endsWith('home')}"/>
  </div>
</template>

<script>
  import Header from "@/components/Header";
  import {AU_EMAIL} from "@/requestServer";
  import {AU_LOCALE_KEY} from "@/i18n";


  export default {
    name: "AUPanelHome",
    components: {
      Header,
    },
    data() {
      return {
        isPc: this.$isPc(),
        router: this.$route.name,
        headerFilter: false
      }
    },
    watch: {
      $route(to) {
        this.router = this.$route.name;
      }
    },
    methods: {
      filterOpen(val) {
        this.headerFilter = val
      }
    },
    created() {
      let TweenMaxScript = document.createElement('script')
      TweenMaxScript.setAttribute('src', 'https://cdn.jsdelivr.net/gh/Joe-Ng-1998/static-elements@au-web/assets/js/TweenMax.min.js')
      document.head.appendChild(TweenMaxScript)

      let ZeSnippetScript = document.createElement('script')
      ZeSnippetScript.setAttribute('id', 'ze-snippet')
      ZeSnippetScript.setAttribute('src', 'https://static.zdassets.com/ekr/snippet.js?key=8b9d5a65-b475-4d1d-9d56-8b8224e21bae')
      document.head.appendChild(ZeSnippetScript)
      ZeSnippetScript.addEventListener(
          'load',
          function (){
            let lang = 'zh-tw';
            if (window.localStorage.getItem(AU_LOCALE_KEY) != null) {
              let currentLang = window.localStorage.getItem(AU_LOCALE_KEY);
              if (currentLang === 'zh-CN') {
                lang = 'zh-cn'
              } else if (currentLang === 'en-US') {
                lang = currentLang
              }
            }
            if (window.localStorage.getItem(AU_EMAIL) && !localStorage.getItem(AU_EMAIL).endsWith('@auvpn.net') && localStorage.getItem(AU_EMAIL).length <= 40) {
              zE('webWidget', 'prefill', {
                name: {
                  value: window.localStorage.getItem(AU_EMAIL)
                      .split('@')[0],
                  readOnly: false // optional
                },
                email: {
                  value: window.localStorage.getItem(AU_EMAIL),
                  readOnly: false // optional
                },
                phone: {
                  value: '',
                  readOnly: false // optional
                }
              });
            }
            zE('webWidget', 'setLocale', lang);

          }
      )
    }
  }
</script>

<style lang="scss">
  .pc-header {
    position: fixed;
    z-index: 1000;
  }

  .header-below {
    margin-top: 88px;
  }
</style>
